.main-register {
    /*max-height: 500px;*/
}

.form-register {
    width: 450px;
    height: 720px;
    border-radius: 10px;

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

    background: rgb(253,40,29);
    background: linear-gradient(221deg, rgba(253,40,29,1) 0%, rgba(206,51,126,1) 50%, rgba(156,63,232,1) 100%);
    background-size: 2000px 2000px;

    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.5);
}

.form-register #main-image {
    width: 100px;
    margin-top: 20px;
}

.form-register #hr-top-left {
    width: 120px;
    left: 60px;
    top: -50px;
    position: relative;
    background-color: white;
    height: 3px;
    border: 0 none;
    border-radius: 5px;
}

.form-register #hr-top-right {
    width: 120px;
    right: -270px;
    top: -70px;
    position: relative;
    background-color: white;
    height: 3px;
    border: 0 none;
    border-radius: 5px;
}

.form-register #h2-cadastro  {
    color: white;
    margin-top: -40px;
}

/*NAME INPUT*/
.form-register .name {
    border-radius: 5px;
    display: flex;
    margin-top: -5px;
    margin-left: 55px;
}

.form-register .label-name-input {
    margin-right: 206px;
    color: white;
}

.form-register .name .div-user-icon {
    width: 45px;
    border: 1.5px solid;
    border-color: white;
    border-radius: 6px 0px 0px 6px;
}
    
.form-register .name .div-user-icon #user-icon {
    margin-top: 5px;
    width: 30px;
    height: 29px;
}

.form-register .name #name-input {
    height: 50px;
    width: 300px;
    border-radius: 0px 6px 6px 0px;
    border: 0 none;
    outline: 0;
}

.form-register #nameHelp {
    color: white;
}


/*EMAIL INPUT*/
.form-register .username {
    border-radius: 5px;
    display: flex;
    margin-top: 1px;
    margin-left: 55px;
    margin-top: -5px
}

.form-register .label-username-input {
    margin-right: 206px;
    color: white;
    margin-top: 10px;
}

.form-register .username .div-user-icon {
    width: 45px;
    border: 1.5px solid;
    border-color: white;
    border-radius: 6px 0px 0px 6px;
}
    
.form-register .username .div-user-icon #user-icon {
    margin-top: 5px;
    width: 30px;
    height: 29px;
}

.form-register .username #username-input {
    height: 50px;
    width: 300px;
    border-radius: 0px 6px 6px 0px;
    border: 0 none;
    outline: 0;
}

.form-register #usernameHelp {
    color: white;
}


/*PASSWORD INPUT*/
.form-register .password {
    border: 1px;
    border-color: white;
    border-radius: 5px;
    display: flex;
    margin-top: 3px;
    margin-bottom: 3px;
    margin-left: 55px;
    margin-top: -5px;
}

.form-register .label-password-input {
    margin-right: 206px;
    color: white;
    margin-top: 10px;
}

.form-register .password .div-padlock-icon {
    width: 45px;
    border: 1.5px solid;
    border-color: white;
    border-radius: 6px 0px 0px 6px;
}

.form-register .password .div-padlock-icon #padlock-icon {
    margin-top: 5px;
    width: 30px;
    height: 29px;
}

.form-register .inputs .password #password-input {
    height: 50px;
    width: 300px;
    border-radius: 0px 6px 6px 0px;
    border: 0 none;
    outline: 0;
}

/*PASSWORD CONFIRMATION FORM*/
.form-register .password-confirmation  {
    border: 1px;
    border-color: white;
    border-radius: 5px;
    display: flex;
    margin-left: 55px;
    margin-top: -5px;
}

.form-register .label-password-input-confirmation {
    margin-right: 206px;
    color: white;
    margin-top: 10px;
}

.form-register .password-confirmation  .div-padlock-icon-confirmation  {
    width: 45px;
    border: 1.5px solid;
    border-color: white;
    border-radius: 6px 0px 0px 6px;
}

.form-register .password-confirmation  .div-padlock-icon-confirmation  #padlock-icon-confirmation  {
    margin-top: 10px;
    width: 30px;
    height: 29px;
}

.form-register .password-confirmation  #password-input-confirmation  {
    height: 50px;
    width: 300px;
    border-radius: 0px 6px 6px 0px;
    border: 0 none;
    outline: 0;
}


/*CHECKBOX INPUT*/
.form-register .div-accept-terms {
    margin-top: 25px;
    margin-right: 86px;
}

.form-register .div-accept-terms #check-input {
    display: none;
}

.form-register .div-accept-terms #check-input + .check-label:before {
    margin-left: -15px;
    content: '';
    display: inline-block;
    width: 1em;
    height: 1em;
    background-color: none;
    border-radius: 3px;
    border: 1px solid white
}

.form-register .div-accept-terms #check-input:checked + .check-label:before {
    background-color: white;
}

.form-register .div-accept-terms .check-label {
    top: 100px;
    color: white;
}


/*BUTTON*/
.form-register .enter-button {
    height: 50px;
    width: 345px;
    margin-top: 15px;
    background-color: #FE5387;
    border-color: #FE5387;
    border-radius: 5px;
    color: white;
    border: 0 none;
    font-size: 15px;
}

.form-register .link {
    margin-top: 20px;
}

.form-register #link-login {
    margin-left: 210px;
    font-size: 15px;
    text-decoration:none;
    color: white;
}

.form-register #hr-bottom {
    background-color: white;
    height: .7px;
    margin-top: 20px;
    
}

